body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@-webkit-keyframes jumpAnim {
    0%   { margin-top:200px;}
    12.5%{ margin-top:145px;}
    25%  { margin-top:120px;}
    37.5%{ margin-top:105px;}
    50%  { margin-top:100px;}
    62.5%{ margin-top:105px;}
    75%  {margin-top:120px;}
    87.5%{ margin-top:145px;}
    100% { margin-top:200px;}
}

@keyframes jumpAnim {
    0%   { margin-top:200px;}
    12.5%{ margin-top:145px;}
    25%  { margin-top:120px;}
    37.5%{ margin-top:105px;}
    50%  { margin-top:100px;}
    62.5%{ margin-top:105px;}
    75%  {margin-top:120px;}
    87.5%{ margin-top:145px;}
    100% { margin-top:200px;}
}

