.MovingObs{
    position: absolute;
    z-index: 0;
}

#gameOverSign li{
    display: inline-block;
    width: 12%
}

#gameOverSign{
    position: absolute;
    top: 100px;
    padding-left: 0px;
    left: 50vw;
    margin-left: -90px;
    text-align: center;
    width: 180px;
}

#theSpecialE{
    padding-right: 4%
}

#playAgainSign{
    position: absolute;
    top: 120px;
    left: 42%;
    right: 42%;
}

#scoreKeep{
    position: absolute;
    top: 10px;
    padding-left: 0px;
    right: 5%;
    width: 70px;
    text-align: center;
}

#scoreKeep li{
    display: inline-block;
    width: 20%
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

